@import "mixins";
@import "normalize";
@import "utilities";

$secondly-color: #ffab18;

html,
body {
  /*height: 100%;*/
}
html {
  max-width: 768px;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  line-height: 1.5;
  font-family: "Hiragino Sans GB", sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}
p {
  margin-top: 0;

  // margin-bottom: 14px;
  margin-bottom: 1rem;
}
p,
div {
  word-break: break-all;
}
img {
  vertical-align: middle;
}
.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}
.container {
  margin-left: 10px;
  margin-right: 10px;
}
.show {
  display: block !important;
}
.hide {
  display: none !important;
}
button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font: inherit;
  color: inherit;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}
input {
  line-height: normal;
}
.close {
  float: right;
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
  color: #000;
  opacity: 0.5;
}
button.close {
  -webkit-appearance: none;
  padding: 0;
  cursor: pointer;
  background: 0 0;
  border: 0;
}

.strong {
  color: $secondly-color;
}
.show {
  display: block !important;
}
.hide {
  display: none !important;
}

.btn {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  padding: 6px 12px;
  font-weight: 400;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-primary {
  color: #fff;
  background-color: $secondly-color;
}
