$gallery-left-right-gap: 6.25%;
@font-face {
  font-family: 'Apple Chancery';
  src: url(../fonts/apple-chancery.eot);
  src: url(../fonts/apple-chancery.eot?#iefix) format('embedded-opentype'), url(../fonts/apple-chancery.woff2) format('woff2'), url(../fonts/apple-chancery.woff) format('woff'), url(../fonts/apple-chancery.ttf) format('truetype');
}
.hint-dismissible {
  padding-right: 30px;
  padding-left: 30px;
}
.hint-dismissible .close {
  position: relative;
  right: -21px;
  color: inherit;
}

.global-hint {
  position: fixed;
  z-index: 1400;
  top: 0;
  left: 0;
  right: 0;
  padding: 15px;
  text-align: center;
  border-bottom: 1px solid hsl(0, 0%, 70%);
  color: #000;
  background-color: hsla(0, 0%, 90%, 0.8);
  -webkit-backdrop-filter: brightness(1.5) blur(20px);
  transition: 0.4s;
  transform: translate(0, -100%);
}
.global-hint.slide-in {
  transform: translate(0, 0);
}

.global-hint .close {
  position: absolute;
  right: 10px;
}

#skrollr-body {
  /*border-left: 1px solid red;
  border-right: 1px solid red;*/
}
#skrollr-body:before,
#skrollr-body:after {
  content: "";
  display: table;
}
#skrollr-body:after {
  clear: both;
}
#loading {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #363434;
  .loading-indicator {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 30px;
    height: 30px;
    margin: auto;
    border-radius: 9999px;
    background-color: #fff;
    animation: loading-indicator 0.3s infinite alternate ease-out both;
  }
  &.loaded-animation {
    .loading-indicator {
      animation: loaded-indicator 0.4s ease-out both;
    }
  }
}
#skrollr-body.loaded-animation {
  animation: body-loaded 1.2s ease-out backwards;

  // .gallery-cover {
  //   animation: gallery-cover .5s .4s ease-out backwards;
  // }
