  //   }
  // }
  //
  // .photo-group-container .photo-group:nth-child(1) {
  //   animation: group-first .6s ease-out both;
  // }
}
@keyframes group-first {
  0% {
    transform: translate3d(30px, -400px, 0) rotateZ(20deg);
    opacity: 0;
  }
  100% {
    transform: rotate(0) translate3d(0,0,0);
    opacity: 1;
  }
}
@keyframes gallery-author {
  0% {
    transform: translate3d(-10px, -600px, 0) rotate(-720deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0);
  }
}
@keyframes gallery-cover {
  0% {
    transform: rotateZ(-10deg) translate3d(-10px, -400px, 0);
    opacity: 0;
  }
  100% {
    transform: rotate(0) translate3d(0,0,0);
    opacity: 1;
  }
}
@keyframes loading-indicator {
  0% {
    transform: scale3d(1, 1) translateZ(0);
  }
  100% {
    transform: scale(.5, .5) translateZ(0);
  }
}
@keyframes loaded-indicator {
  100% {
    transform: scale(30, 30) translateZ(0);
  }
}
@keyframes body-loaded {
  0% {
    opacity: 0;
    transform: translate3d(0, 40px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.gallery-header {
  text-align: center;
  margin-top: 130px;
  margin-bottom: 0;
  .gallery-date {
    font-size: 25px;
    line-height: 1;
    margin-bottom: 17px;
    font-family: "Apple Chancery", serif;
  }
  .gallery-title {
    font-size: 26px;
    line-height: 1;
    margin-bottom: 26px;
    font-weight: 500;
  }
  .gallery-cover {
    margin-left: 36px;
    margin-right: 36px;
    margin-bottom: 36px;
  }
  .gallery-cover-inner {
    height: 0;
    padding-bottom: percentage(138/248);
    img {
      width: 100%;
    }
  }
}
.gallery-cover {
  margin-left: 36px;
  margin-right: 36px;
  .photo-group-inner {
    padding-bottom: percentage(276px/496px);
  }
  .photo-item:nth-child(1) {
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    .photo-item-wrapper {
      padding-bottom: percentage(276px/496px);
    }
  }
}
.gallery-description {
  height: 100px;
  color: #757575;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 11px;
}
.gallery-authors {

  // position: relative;
  // z-index: 100;
  height: 40px;

  // background: url(../img/gallery-authors-bg.png) repeat-x;
  // background-image: image-set(url(../img/gallery-authors-bg.png) 1x, url(../img/gallery-authors-bg@2x.png) 2x);
  margin-left: 36px;
  margin-right: 36px;
  margin-bottom: 36px;
  overflow-x: auto;

  // overflow: hidden;
  -webkit-overflow-scrolling: touch;
  font-size: 0;
  white-space: nowrap;
  .gallery-authors-inner {
    height: 40px;
  }
}
.gallery-author {
  display: inline-block;
}
.gallery-authors img {
  width: 40px;
  height: 40px;
}
.gallery-footer {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 70px;
  background: url(../images/gallery-footer-deco.png) no-repeat 50% 90%;
  background-image: image-set(url(../images/gallery-footer-deco.png) 1x, url(../images/gallery-footer-deco@2x.png) 2x);
  .gallery-footer-text {
    border-radius: 9999px;
    height: 50px;
    width: 50px;
    line-height: 50px;
    margin: auto;
    font-size: 9px;
    color: #fff;
    background: #000;
    text-align: center;
  }
}
.progress {
  height: 4px;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 4px;
}
.progress-bar {
  float: left;
  width: 0;
  height: 4px;
  font-size: 12px;
  line-height: 20px;
  color: rgba(255,255,255,.75);
  text-align: center;
  background-color: $secondly-color;
  transition: width 0.6s ease;
}
.toolbar-bottom {
  position: fixed;
  display: none;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50px;
  overflow: hidden;
  font-size: 12px;
  color: #fff;
  background: #000;
  background-color: rgba(0,0,0,.8);
  -webkit-backdrop-filter: brightness(1.5) blur(20px);
  a {
    color: inherit;
    text-decoration: none;
  }
  &.active {
    display: block;
  }
}

.toolbar-upload {
  line-height: 25px;
  .state {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    line-height: 50px;
  }
  .open-app {
    color: inherit;
    float: left;
    padding-left: 20px;
    padding-right: 20px;
    text-decoration: none;
  }
  .upload-file-container {
    position: relative;
    float: right;
    padding-left: 20px;
    padding-right: 20px;
    overflow: hidden;
    input {
      position: absolute;
      left: 0;
      top: 0;
      margin: 0;
      font-size: 200px;
      opacity: 0;
      cursor: pointer;
    }
  }
  .state-upload {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: $gallery-left-right-gap;
    padding-right: $gallery-left-right-gap;
    .state-progress {
      flex: 1;
      margin-left: 10px;
      margin-right: 10px;
    }
  }
  .state-normal {

  }
}

.toolbar-download {
  .download-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding-left: 10px;
    padding-right: 10px;
    .logn-and-slogan {
      display: inline-flex;
      font-size: 12px;
      align-items: center;
    }
    .slogan {
      display: inline-flex;
      flex-direction: column;
      .secondly-title {
        font-size: 10px;
      }
    }
    .logo {
      margin-right: 10px;
    }
    .download-btn {
      padding: 6px 18px;
      color: #000;
      background: #fff;
      border-radius: 5px;
      letter-spacing: 1px;
    }
  }
}

.toolbar-invitation-code {
  a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: inherit;
  }

}

body.with-toolbar-bottom {
  padding-bottom: 50px;
}

.modal {
  display: none;
  position: fixed;
  z-index: 10001;
  left: 0;
  right: 0;
  top: 50%;
  width: 240px;
  margin: auto;
  padding-top: 15px;
  // transform: translate(0, -50%);
  background: rgba(255,255,255,.95);
  // -webkit-backdrop-filter: brightness(1.5) blur(20px);
  border-radius: 10px;
  margin-top: -85px;
  .close {
    position: absolute;
    top: 6px;
    right: 6px;
    line-height: 1;
    font-size: 22px;
  }
}

.modal-body {
  padding: 15px;

}

.modal-footer {
  padding: 10px 15px;
  border-top: 1px solid #828282;
  .btn-block {
    padding-left: 0;
    padding-right: 0;
    width: auto;
    margin-left: -15px;
    margin-right: -15px;
    color: #000;
  }
}

.modal.show {
  animation: popup 0.35s ease-in-out;
}

.modal-backdrop {
  display: none;
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.75);
  -webkit-backdrop-filter: brightness(1.5) blur(5px);
  background-color: rgba(0,0,0,0);
  transition: background-color 0.3s;
}
.modal-backdrop.show {
  background-color: rgba(0,0,0,.7);
}

.modal-invitation-code {
  text-align: center;
  user-select: none;
  .invitation-code {
    display: inline-block;
    font-weight: bold;
    padding-left: 20px;
    padding-right: 20px;
    line-height: 25px;
    color: #fff;
    background: $secondly-color;
    border-radius: 5px;
    user-select: text;
  }
}

@keyframes popup {
	0% {
	  opacity:0;
	  transform:scale3d(0.5, 0.5, 1);
  }
	45% {
	  transform:scale3d(1.12, 1.12, 1);
  }
	80% {
	  opacity:1;
	  transform:scale3d(0.9, 0.9, 1);
  }
	100% {
	  opacity:1;
	  transform:scale3d(1, 1, 1);
  }
}

.guide-to-open-in-browser {
  display: none;
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.75);
  -webkit-backdrop-filter: brightness(1.5) blur(5px);
  color: #fff;
  background-color: rgba(0,0,0,0);
  transition: background-color 0.3s;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  .guide-right-top-corner {
    text-align: right;
    margin: 5px 15px 10px 0;
    padding: 181px 52px 0 0;
    background: url(../images/right-top-cornor-arrow.png) no-repeat 100% 0;
    background-image: image-set(url(../images/right-top-cornor-arrow.png) 1x, url(../images/right-top-cornor-arrow@2x.png) 2x);
  }
}

.guide-to-open-in-browser.show {
  background-color: rgba(0,0,0,.7);
}
